/* src/components/BookingDetails.css */
.history-details {
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 10px;
    margin: 10px 0;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    font-Family: monospace;
    background-color: rgb(58, 146, 58);
    color: white;
  }
  
  .history-summary {
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
  }
  
    .name {
    font-weight: bold;
    margin-right: 10px;
    font-family: monospace;
    white-space: pre;
    font-style: italic
  }
  
  .h-toggle-button {
    background-color: #3498db;
    color: white;
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .history-extra-details {
    margin-top: 10px;
    padding-top: 10px;
    border-top: 1px solid #ddd;
    font-size: 12px;
    font-weight: bold;
    font-style: italic;
  }
  
  .history-extra-details p {
    margin: 5px 0;
  }
  .field-group {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
  }

  .checkmark-icon {
    width: 20px;
    height: 20px;
    vertical-align: middle;
  }
  