/* src/index.css or src/App.css */
html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
body {
  background-color: white;
  color: #333; /* Dark grey for contrast */
  font-family: 'Roboto', sans-serif; /* Example font, can be any other */
  margin: 0;
  padding: 0;
  background-color:antiquewhite
}

.app {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.content {
  flex: 1;
  overflow-y: auto;
}

/* src/components/Footer.css */
.footer {
  background-color: #1E3A5F;  /* Dark blue, same as part of header gradient */
  color: white;
  padding: 15px 0px;
  box-shadow: 0 -4px 8px rgba(0, 0, 0, 0.2);  /* Drop shadow for the footer */
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  position: fixed;
  bottom: 0;
  font-family: monospace;
}

.footer a {
  color: white;
  text-decoration: none;
  font-size: 13px;
  padding-left: 5px;
  padding-right: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer a.active {
  font-weight: bold;
  text-decoration: underline;

}

.footer a:hover {
  color: #e0e0e0;
}

.screen {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 60px); /* Adjust this if footer height changes */
  font-size: 24px;
}
.hidden {
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
  background-color: #fef9f9;
}