/* src/components/Card.css */

.card {
  background-color: white;
  border: 1px solid #e0e0e0; /* Light grey border */
  border-radius: 8px;
  padding: 16px;
  margin: 16px 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
  display: flex;
  flex-direction: column;
  font-family: monospace;
}

  .field-group {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
  }
  
  .field-group p, .field-group input {
    flex: 1;
    margin-right: 10px;
  }
  
  .field-group p:last-child, .field-group input:last-child {
    margin-right: 0;
  }
  
  .field-group input {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .button-group {
    display: flex;
    justify-content: flex-end;
  }
  
  
  