/* Available.module.css */

:root {
  --primary-color: #4CAF50;
  --secondary-color: #ffffff;
  --text-color: #333;
  --card-background: #f9f9f9;
  --border-color: #ddd;
  --shadow-color: rgba(0, 0, 0, 0.1);
}

.container {
  padding: 20px;
  text-align: center;
  background-color: var(--secondary-color);
  color: var(--text-color);
  font-family: 'Roboto', sans-serif;
  background-color: white;
}

.label {
  font-size: 18px;
  margin-bottom: 10px;
  color: var(--primary-color);
  text-align: center;
}


.sub-header {
  font-size: 24px;
  margin: 20px 0;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.card {
  border: 1px solid var(--border-color);
  border-radius: 10px;
  padding: 15px;
  margin: 10px;
  width: 90%;
  max-width: 300px;
  text-align: center;
  background-color: var(--card-background);
  box-shadow: 0 4px 8px 0 var(--shadow-color);
  transition: transform 0.2s;
}

.card:hover {
  transform: scale(1.05);
}

.block-button {
  margin-top: 10px;
  padding: 8px 16px;
  background-color: #f44336;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.block-button:hover {
  background-color: #d32f2f;
}

.unblock-button {
  background-color: #4CAF50;
}

.unblock-button:hover {
  background-color: #388E3C;
}

.blocked {
  background-color: rgba(244, 67, 54, 0.1);
  border: 1px solid #f44336;
}

@media (min-width: 600px) {
  .date-picker-container {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 40px;
  }
  .label {
    margin-bottom: 0;
    margin-right: 10px;
    text-align: right;
  }
}

@media (min-width: 900px) {
  .card {
    width: 30%;
  }
}
.search-bar {
  display: flex;
  align-items: center;
  border: 2px solid rgb(255, 255, 255); /* Matches the lighter part of your header gradient */
  border-radius: 8px;
  padding: 6px 10px;
  margin: 16px auto; /* Centers the search bar and adds vertical margin */
  background-color: white;
  justify-content: space-between;
  width: 90%; /* Use percentage to fit within the screen */
  max-width: 600px; /* Optional: Limit the maximum width for larger screens */
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}
.search-bar input {
  flex: 1;
  border: none;
  outline: none;
  padding: 8px;
  font-size: 16px;
  color: #333;
}

.search-bar .search-icon {
  margin-left: 8px;
}